<template>
  <div class="homeBox" v-if="time">
    <Header :time="time"></Header>
    <Banner :bannerData="bannerList"></Banner>
    <van-list :immediate-check="false" v-model="loading" @load="loadMore">
      <NewsItem v-for="item in list" :key="item.id" :item="item"></NewsItem>
    </van-list>
  </div>
</template>
<script>
import Header from "../components/Header.vue";
import Banner from "../components/Banner.vue";
import NewsItem from "../components/NewsItem.vue";
export default {
  data() {
    return {
      list: [],
      bannerList: [],
      time: "",
      lastTime: "",
      loading: false
    };
  },
  components: {
    Header,
    Banner,
    NewsItem
  },
  async created() {
    let result = await this.$api.latest();
    let { date, stories, top_stories } = result;
    console.log(result,'result');
    
    this.time = date;
    this.lastTime = date;
    this.list = stories;
    this.bannerList = top_stories;
  },
  methods: {
    loadMore() {
      let timer = setTimeout(async () => {
        let result = await this.$api.before(this.lastTime);
        let { date, stories } = result;
        this.loading = false;
        this.lastTime = date;
        this.list = this.list.concat(stories);
        clearTimeout(timer);
      }, 500);
    }
  }
};
</script>
<style lang="less">
.homeBox {
  padding-top: 1.2rem;
}

.van-list {
  padding: 0 0.2rem;
}
</style>