import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../pages/Home.vue';
import Detail from '../pages/Detail.vue';
Vue.use(VueRouter);
const router = new VueRouter({
	mode: "hash", //=>HASH VS BROWSER 路由
	routes: [{
		path: '/',
		name: 'home',
		component: Home
	}, {
		path: '/detail/:id',
		name: 'detail',
		component: Detail
	}, {
		path: '*',
		redirect: '/' //=>也可以重定向到404页面
	}]
});
export default router;